















import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import Icon from '@/components/Icon.vue';

@Component({
  components: {Icon},
})

export default class Qrcode extends Vue {
  pointerFine = false
  created(){
    if (matchMedia('(pointer:fine)').matches) {
      this.pointerFine = true
    }

  }
}
